<template>
  <div>
    <ul>
      <li v-for="(item, index) in list"
          :key="index">
        <router-link :to="{name:'news',query:{type:item.type,id:item.id}}">{{item.title}}</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: {
    list: Array
  }
}
</script>
<style lang="scss" scoped>
ul {
  padding: 20px;
  height: 200px;
  li {
    width: 300px;
    overflow: hidden;
    text-align: left;
    line-height: 26px;
    text-overflow: ellipsis;
    color: #333333;
    font-size: 16px;
    line-height: 26px;
  }
}
</style>



