<template>
  <div class="content">
    <div class="aside">新闻资讯</div>
    <div class="content-right">
      <div class="title">
        当前位置*新闻资讯
      </div>
      <div class="inside">

        <el-tabs v-model="activeName"
                 type="card"
                 @tab-click="handleClick">
          <!-- <el-tab-pane label="活动"
                       name="acitve">
            <list-item :list="list">
            </list-item>
          </el-tab-pane> -->
          <el-tab-pane label="新闻"
                       name="news">
            <list-item :list="list">
            </list-item>
          </el-tab-pane>
          <el-tab-pane label="公告"
                       name="notice">
            <list-item :list="list">
            </list-item>
          </el-tab-pane>

        </el-tabs>
        <div class="page">
          <el-pagination @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :current-page.sync="searchForm.pageNum"
                         :page-sizes="[10, 20, 30, 40]"
                         :page-size="10"
                         layout="sizes, prev, pager, next">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>

</template>
<script >
import listItem from '@/components/news/ListItem.vue'
export default {
  data () {
    return {
      searchForm: {
        pageNum: 1,
        pageSize: 10
      },
      list: [
        {
          title: '测试标题',
          createdTime: '1568214274739'
        }
      ],
      activeName: 'news'
    }
  },
  methods: {
    handleSizeChange (val) {
      this.searchForm.pageSize = val
      this.getData()

    },
    handleCurrentChange (val) {
      this.searchForm.pageNum = val
      this.getData()
    },
    handleClick (type) {
      switch (this.activeName) {
        case 'news':
          this.reset()
          this.getData()
          break;
        case 'notice':
          this.reset()
          this.getData()
          break;
        case 'active':
          this.reset()
          this.getData()
          break;
        default:
          break;
      }
    },
    getData (type) {
      let api = '/main/getNews'
      if (this.activeName !== 'news') {
        api = '/main/getNotice'
      }
      this.$get(api, {
        params: {
          pageNum: this.searchForm.pageNum - 1,
          pageSize: this.searchForm.pageSize
        }
      }).then(res => {
        this.list = res

      })
    },
    reset () {
      Object.assign(this.searchForm, {
        pageNum: 1,
        pageSize: 10
      })
    }

  },
  created () {
    this.getData()
  },
  components: {
    listItem
  }
}
</script>



